import React from 'react';
import { Link } from 'react-router-dom';

const text = {
    textDecoration: 'none',
    color: '#000000'
  }

const LinkBtn = ({ to, label, children, click }) => {
    return (
      <Link onClick={click} style={text} to={to}>
        {children}
        <span>{label}</span>
      </Link>
    )
  }
  
  
  export default LinkBtn;


