import React from 'react';
import Item from './Item';
import Link from './Link';
import { connect } from 'react-redux';
import { RECEIVE_ITEM } from '../../Const/const';

const ListItem = props => {
    return (
        <div>
            {props.data.map((item, index) => (
                <Link 
                    click={() => props.receiveItem(item)} 
                    key={Math.random()} 
                    to={`post/${item.id}`}>
                        <Item    
                            key={Math.random()}
                            id={item.id}
                            title={item.title}
                            body={item.body} />
                        </Link>
            ))}
        </div>
    )
}

const mapDispatchToProps = dispatch => {
    return {
        receiveItem: (item) => dispatch({type: RECEIVE_ITEM, payload: item})
    }
}

export default connect(null, mapDispatchToProps)(ListItem);


