import React, { Component } from 'react';
import { connect } from 'react-redux';
import ListItem from './Item/ListItem';
import { FILTER_LIST, API_CALL_REQUEST } from '../Const/const';
//import Loadable from 'react-loadable';

// const ListItem1 = Loadable({
//             loader: () => import('./Item/ListItem'),
//             loading: () => <div>Loading...</div>
// })

class MainPage extends Component {
    state = {
        value: ''
    }
    componentDidMount() {
        this.props.fetchData();
    }
    inputHandler = e => {
        this.setState({value: e.target.value})
        this.props.filterList(this.state.value)
    }
    render() {
        const { data } = this.props
        return (
            <div>
                <input className='input' placeholder='Find your message...' onChange={this.inputHandler} value={this.state.value} />
                <ListItem data={data} />
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        data: state.init.ListItem.filter(item => item.title.includes(state.filter))
    }
}

const mapDispatchToProps = dispatch => {
    return {
        filterList: (value) => dispatch({type: FILTER_LIST, payload: value}),
        fetchData: () => dispatch({type: API_CALL_REQUEST})
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(MainPage);


